.app-wrapper {
   display: -ms-grid;
   display: grid;
   width: 1200px;
   margin: 0 auto;
   grid-template-areas:
      "h h"
      "n c";
   -ms-grid-rows: 60px 1fr;
   grid-template-rows: 60px 1fr;
   -ms-grid-columns: 2fr 10fr;
   grid-template-columns: 2fr 10fr;
   /* grid-gap: 10px; */
}
.app-wrapper-content {
   -ms-grid-row: 2;
   -ms-grid-column: 2;
   grid-area: c;
   background: #4d7bc5;
}
input {
   color: black;
}
